body{
    background-image:url(https://t4.ftcdn.net/jpg/07/99/31/83/240_F_799318371_5WusQtlami5AHGOA1AYNLvrh252USr3E.jpg);
    background-repeat: no-repeat;
    background-size:cover;
}
.board-caintainer{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: rgb(241, 243, 243);
    font-size: 2.5rem;
    user-select: none;
}
.square{
    border: 1px solid;
    height:150px;
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.won{
    padding:20px;
}
.play-again{
    font-size: 2rem;
    margin-right: 20px;
    border:2px solid;
    cursor: pointer;
}
.draw{
    padding: 20px;
}